.front-screen
{
    width: 100%;
    padding: 5rem 1rem;
    display: flex;
    justify-content: space-around;
}

.front-screen .filter-container
{
    background: #ffffff;
    width: 18%;
}

.front-screen .product-container
{
    background: #ffffff;
    width: 78%;
}

.product-container
{
    padding: 1rem;
}

.products .product-box
{
    height: 22rem;
    width: 14rem;
    /* border: 2px solid red; */
    margin-bottom: 1rem;
    transition: all 0.1s ease-in-out;
}

.products .product-box .img-box
{
    height: 63%;
    width: 100%;
    /* margin-bottom: 1rem; */
}

.img-box img
{
    height: 100%;
    width: 100%;
}

.producrt-desc
{
    margin: 1rem;
}


.products
{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.search-box input
{
    width: 100%;
    height: 2rem;
    border-radius: 20px;
    outline: none;
    border: 1px solid gray;
    padding: 1rem;
}

.search-box, .filter-box, .category-box
{
    padding: 1rem;
    margin-bottom: 1rem;
    border-bottom: 1px solid gray;
}


.filter-box p
{
    margin-bottom: 1rem;
}

.price-range p, .category-box p
{
    font-size: 1rem;
}

.filter-box input
{
    width: 100%;
}

.category-box select
{
    margin-top: 1rem;
    border: none;
    width: 100%;
    height: 2rem;
    outline: none;
}

.producrt-desc p
{
    font-size: 0.8rem;
}

.add-cart
{
    border: none;
    outline: none;
    background: #fa5c28;
    color: #ffffff;
    padding: 0.5rem 1rem;
    margin-top: 0.3rem;
    font-size: 1rem;
}

.add-cart:hover
{
    background: #992802;
}

.product-box:hover
{
    box-shadow: 0px 0px 10px 5px rgb(211, 211, 211);
}

.filter-box input[type='range']::-webkit-slider-runnable-track {
    color: #6E3CBC;
}
  
.filter-box input[type='range']::-webkit-slider-thumb {
    color: #6E3CBC;
}

/* //-----------------------------------------media query--------------------------- */
@media (min-width: 280px)
{
    .front-screen
    {
        flex-direction: column;
    }
    .front-screen .filter-container
    {
        width: 100%;
        margin-bottom: 1rem;
    }
    .front-screen .product-container
    {
        width: 100%;
    }
    .nav .logo
    {
        font-size: 0.5rem;
    }
    .nav-container
    {
        padding: 10px 0px;
    } 
  
}  


@media (min-width: 375px)
{
    .front-screen
    {
        flex-direction: column;
    }
    .front-screen .filter-container
    {
        width: 100%;
        margin-bottom: 1rem;
    }
    .front-screen .product-container
    {
        width: 100%;
    }
  
}  

@media (min-width: 576px)
{
    .front-screen
    {
        flex-direction: column;
    }
    .front-screen .filter-container
    {
        width: 100%;
    }
    .front-screen .product-container
    {
        width: 100%;
    }
    .filter-container .filter-box, .category-box
    {
        width: 80%;
        margin: auto;
    }
    .category-box
    {
        border: none;
    }

}

@media (min-width: 768px)
{
    .front-screen .filter-container
    {
        width: 30%;
    }
    .front-screen .product-container
    {
        width: 68%;
    }
    .front-screen
    {
        flex-direction: row;
    }
    .filter-box, .category-box
    {
        padding: 1rem 0;
        border-bottom: 1px solid gray;
    }
    .front-screen .filter-container
    {
        margin-bottom: 0;
    }
    
}

@media (min-width: 1200px)
{
    .front-screen .filter-container
    {
        width: 25%;
    }
    .front-screen .product-container
    {
        width: 73%;
    }
    .front-screen .filter-container
    {
        margin-bottom: 0;
    }
    .filter-container .category-box
    {
        border-bottom: 1px solid gray;
    }
 
}
