.cart-page 
{
    padding: 5rem 1rem;
    width: 80%;
    /* height: 90vh; */
    margin: auto;
}

.cart-header {
    position: relative;
    width: 90%;
    margin: auto;
    display: flex;
    justify-content: space-between;
}

.cart-items-container {
    background: #ffffff;
    width: 90%;
    /* height: 75%; */
    margin: 2rem auto;
    padding: 1rem;
}

.cart-items {
    width: 100%;
    height: 100%;
}

.cart-info {
    display: flex;
    padding: 1rem;
    justify-content: space-between;
    align-items: center;
}

.cart-info .item-img {
    height: 8rem;
    width: 8rem;
    box-shadow: 0px 0px 2px gray;
}

.cart-info .item-img img {
    height: 100%;
    width: 100%;
}

.cart-info .item-desc {
    padding: 1rem;
    font-size: 1.5rem;
    width: 10rem;
    text-align: center;
}

.cart-info .qty-counter {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
}

.cart-info .qty-counter input {
    width: 2rem;
    height: 2rem;
    text-align: center;
    margin: auto 1rem;
}

.cart-info .price {
    padding: 0.5rem;
    font-size: 1.5rem;
    text-align: center;
    width: 7rem;
}

.cart-info .delete {
    padding: 0.5rem;
    color: red;
}

.cart-button {
    margin: auto;
    display: flex;
    justify-content: space-between;
    margin-top: 1rem;
}

.remove-all {
    padding: 0.7em 1rem;
    border: none;
    outline: none;
    background: red;
    color: #ffffff;
    margin: auto 0px;
}

.cart-button p 
{
    font-size: 1rem;
    text-align: center;
    background: #ffffff;
    padding: 0.5rem;
}

.cart-item-count {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    height: 22px;
    width: 22px;
    border: 1px solid #ffffff;
    right: -8px;
    top: -15px;
    background: rgb(247, 72, 3);
    color: #ffffff;
    font-weight: bold;
    border-radius: 50%;
}

.cart-box a {
    text-decoration: none;
}

/* -----------------------------------media query --------------------*/
@media (max-width: 280px)  
{
    .cart-page
    {
        padding: 5rem 0;
        width: 100%;
    }    
    .cart-page .empty-cart-heading
    {
        margin-top: 1.5rem;
    }
}



@media (max-width: 375px)
{
    .cart-info
    {
        flex-direction: column;
    }
    .cart-button p
    {
        font-size: 0.8rem;
    }
    .cart-page
    {
        padding: 5rem 0;
        width: 100%;
    }    
    .cart-page .empty-cart-heading
    {
        margin-top: 1.5rem;
    }
}


@media (max-width: 576px)
{
    .cart-info
    {
        flex-direction: column;
    }
}


@media (max-width: 768px)
{
    .cart-button
    {
        margin-top: 1rem;
    }
    .cart-items-container
    {
        width: 100%;
    }
    .cart-info .qty-counter 
    {
        padding: 0;
    }
    .cart-info .price
    {
        width: 8rem;
    }
}

@media (max-width: 992px)
{
    .cart-button
    {
        margin-top: 1rem;
    }
    .cart-items-container
    {
        width: 100%;
    }
    .cart-info .qty-counter 
    {
        padding: 0;
    }
    .cart-info .price
    {
        width: 8rem;
    }
}

