.nav-container
{
    width: 100%;
    position: fixed;
    /* z-index: 5; */
    padding-top: 1.2rem;
    padding-bottom: 1rem;
    /* width: 100vw; */
    background: #6E3CBC;
}

.nav
{
    width: 75%;
    display: flex;
    justify-content: space-between;
    margin: auto;
    align-items: center;
}

.cart
{
    color: #ffffff;
}

.logo a
{
    text-decoration: none;
    color: #ffffff;
}

.nav [type="checkbox"],span 
{
    display: none;
}

.cart-box a
{
    text-decoration: none;
}

.cart-box
{
    position: relative;
}

.cart-count
{
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 20px;
    width: 20px;
    border: 1px solid #ffffff;
    right: -8px;
    top: -10px;
    background: rgb(247, 72, 3);
    color: #ffffff;
    font-weight: bold;
    border-radius: 50%;
}